import React from 'react';
import PropTypes from 'prop-types';
import './skl-ds-card.scss';

export const Card = ({ color, }) => {
  const styles = {
    backgroundColor: color,
  };

  return <div className="skl-ds-card" style={styles} />;
};

Card.propTypes = {
  color: PropTypes.string,
};

Card.defaultProps = {
  color: '#fff',
};
